@font-face {
    font-family: Muli;
    src: url(../../assets/media/Muli.19af2017.ttf) format("truetype");
    font-weight: 600;
    font-style: normal
}

@font-face {
    font-family: Muli;
    font-style: normal;
    font-weight: 500;
    src: local("Muli Regular"), url(../../assets/media/Muli-Regular.4f394d45.woff) format("woff")
}

@font-face {
    font-family: Muli;
    font-style: normal;
    font-weight: 600;
    src: local("Muli SemiBold"), url(../../assets/media/Muli-SemiBold.901353e8.woff) format("woff")
}

@font-face {
    font-family: Muli;
    font-style: normal;
    font-weight: 700;
    src: local("Muli Bold"), url(../../assets/media/Muli-Bold.1d64aefe.woff) format("woff")
}

html {
    font-size: 15px
}

:root {
    scroll-behavior: unset
}

#__next {
    overflow: hidden
}

img {
    max-width: 100%;
    width: auto;
    height: auto
}

body {
    font-family: Muli;
    color: #4f555a;
    background-color: #fff;
    font-size: 15px;
    overflow-x: unset;
    font-size: 1rem
}

@media(max-width:767px) {
    body {
        font-size: 14px
    }
}

p {
    font-size: 16px;
    color: #4f555a;
    line-height: 1.8em
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #212158;
    font-family: Roboto Slab, serif
}

ul {
    padding-left: 0;
    margin: 0;
    list-style: none
}

a {
    transition: all .2s
}

a,
a:hover {
    text-decoration: none
}

h2.hidden {
    display: none
}

.sr-only {
    display: none !important;
    opacity: 0;
    visibility: hidden
}

.page-wrapper {
    position: relative;
    overflow: hidden
}

.wow {
    visibility: hidden
}

.fi:before {
    margin: 0
}

.section-padding {
    padding: 120px 0
}

@media(max-width:767px) {
    .section-padding {
        padding: 80px 0
    }
}

.contact-validation-active .error-handling-messages {
    width: 100% !important;
    margin-top: 15px !important
}

.contact-validation-active label.error {
    color: red;
    font-size: .9333333333rem;
    font-weight: 400;
    margin: 5px 0 0;
    text-align: left;
    display: block
}

.contact-validation-active #loader {
    display: none;
    margin-top: 10px
}

.contact-validation-active #loader i {
    font-size: 30px;
    font-size: 2rem;
    color: #ef5f34;
    display: inline-block;
    animation: rotating 2s linear infinite
}

.contact-validation-active #error,
.contact-validation-active #success {
    width: 100%;
    color: #fff;
    padding: 5px 10px;
    font-size: 16px;
    text-align: center;
    display: none
}

@media(max-width:767px) {

    .contact-validation-active #error,
    .contact-validation-active #success {
        font-size: 15px
    }
}

.contact-validation-active #success {
    background-color: #009a00;
    border-left: 5px solid green;
    margin-bottom: 5px
}

.contact-validation-active #error {
    background-color: #ff1a1a;
    border-left: 5px solid red
}

@keyframes rotating {
    0% {
        transform: rotate(0deg)
    }

    to {
        transform: rotate(1turn)
    }
}

.rotating {
    animation: rotating 5s infinite alternate
}

.back-to-top {
    background-color: rgba(239, 95, 52, .7);
    width: 45px;
    height: 45px;
    line-height: 45px;
    border: 2px solid #ef5f34;
    border-radius: 45px;
    text-align: center;
    display: none;
    position: fixed;
    z-index: 999;
    right: 15px;
    bottom: 15px;
    transition: all .2s
}

@media(max-width:991px) {
    .back-to-top {
        width: 35px;
        height: 35px;
        line-height: 35px
    }
}

.back-to-top:hover {
    background-color: #ef5f34
}

.back-to-top i {
    font-size: 18px;
    font-size: 1.2rem;
    color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%)
}

.mfp-wrap {
