.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
    opacity: 1
}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
    opacity: .25
}

.slick-next:before,
.slick-prev:before {
    font-family: slick;
    font-size: 20px;
    line-height: 1;
    opacity: .75;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slick-prev {
    left: -25px
}

[dir=rtl] .slick-prev {
    right: -25px;
    left: auto
}

.slick-prev:before {
    content: "←"
}

[dir=rtl] .slick-prev:before {
    content: "→"
}

.slick-next {
    right: -25px
}

[dir=rtl] .slick-next {
    right: auto;
    left: -25px
}

.slick-next:before {
    content: "→"
}

[dir=rtl] .slick-next:before {
    content: "←"
}

.slick-dotted.slick-slider {
    margin-bottom: 30px
}

.slick-dots {
    position: absolute;
    bottom: -25px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center
}

.slick-dots li {
    position: relative;
    display: inline-block;
    margin: 0 5px;
    padding: 0
}

.slick-dots li,
.slick-dots li button {
    width: 20px;
    height: 20px;
    cursor: pointer
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    padding: 5px;
    color: transparent;
    border: 0;
    outline: none;
    background: transparent
}

.slick-dots li button:focus,
.slick-dots li button:hover {
    outline: none
}

.slick-dots li button:focus:before,
.slick-dots li button:hover:before {
    opacity: 1
}

.slick-dots li button:before {
    font-family: slick;
    font-size: 6px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: "•";
    text-align: center;
    opacity: .25;
    color: #000;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #000
}

/*--------------------------------------------------------------

# Team One
--------------------------------------------------------------*/
.team-one {
    position: relative;
    display: block;
    padding: 120px 0 90px;
  }
  
  .team-one__single {
    position: relative;
    display: block;
    margin-bottom: 30px;
  }
  
  .team-one__img {
    position: relative;
    display: block;
    border-radius: var(--oxpins-bdr-radius);
    overflow: hidden;
    z-index: 1;
  }
  
  .team-one__img:before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    background-color: rgba(var(--oxpins-black-rgb), .30);
    width: 0%;
    transform: translateY(100%);
    border-radius: var(--oxpins-bdr-radius);
    transition: all 500ms ease;
    z-index: 1;
  }
  
  .team-one__single:hover .team-one__img:before {
    transform: translateY(0);
    width: 100%;
  }
  
  .team-one__img img {
    width: 100%;
    border-radius: var(--oxpins-bdr-radius);
    transition-delay: .1s;
    transition-timing-function: ease-in-out;
    transition-duration: .7s;
    transition-property: all;
  }
  
  .team-one__single:hover .team-one__img img {
    transform: scale(1.1) rotate(2deg);
  }
  
  .team-one__content {
    position: relative;
    display: block;
    background-color: var(--oxpins-black);
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
    border-bottom-left-radius: var(--oxpins-bdr-radius);
    border-bottom-right-radius: var(--oxpins-bdr-radius);
    padding: 27px 0 28px;
  }
  
  .team-one__name {
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: -0.04em;
  }
  
  .team-one__name a {
    color: var(--oxpins-white);
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
  }
  
  .team-one__name a:hover {
    color: var(--oxpins-primary);
  }
  
  .team-one__sub-title {
    font-size: 16px;
    color: var(--oxpins-primary);
    font-weight: 700;
    font-family: 'Poppins';
    line-height: 16px;
  }
  
  .team-one__social {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    background-color: var(--oxpins-primary);
    padding: 10px 22px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    transform: scaleY(0.0) translateX(-50%);
    transform-origin: center;
    transform-style: preserve-3d;
    -webkit-transition: all 0.4s linear;
    -o-transition: all 0.4s linear;
    transition: all 0.4s linear;
    transform-origin: bottom center;
    z-index: 2;
  }
  
  .team-one__single:hover .team-one__social {
    transform: scaleY(1.0) translateX(-50%);
    -webkit-transition-delay: 500ms;
    -moz-transition-delay: 500ms;
    -ms-transition-delay: 500ms;
    -o-transition-delay: 500ms;
    transition-delay: 500ms;
  }
  
  .team-one__social a {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 15px;
    color: var(--oxpins-black);
  }
  
  .team-one__social a+a {
    margin-left: 15px;
  }

  @media (max-width:375px) {
    .theme-btn {
        width: 90px;
    }
}

